@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css);

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{min-height:100%}body{height:100%;font-family:'Roboto', sans-serif;font-size:18px}button{padding:16px;font-size:16px;background-color:black;color:white;border-radius:8px;cursor:pointer}h3,h4{margin-bottom:0}.clearfix{clear:both}.fade-appear,.fade-enter{opacity:0;z-index:1}.fade-appear-active,.fade-enter.fade-enter-active{opacity:1;transition:opacity 500ms linear 150ms}.fade-exit{opacity:1}.fade-exit.fade-exit-active{opacity:0;transition:opacity 150ms linear}#page-body{max-width:950px;min-height:auto;margin:auto;padding:25px;padding-top:64px}.page-title{font-family:'Rock Salt', cursive;font-size:1.75em;margin-bottom:0px;color:black}.underline{color:black;text-decoration:none;border-bottom:none;position:relative}.underline::after{content:'';width:100%;position:absolute;left:0;bottom:-1px;height:3px;background:#cc3b8b;background:linear-gradient(to right, #421575, #cc3b8b)}.projectImg{width:100%;height:300px;max-width:375px;object-fit:contain}.grid-container{height:auto;display:grid;grid-template-columns:repeat(auto-fit, minmax(310px, 1fr));grid-gap:1rem;padding-bottom:50px;padding-top:0px;margin:0 auto}.grid-item{height:auto;max-width:400px;margin:50px auto 10px auto}.grid-item-text{height:auto;max-width:400px;margin:0px auto}.project-title{font-family:'Rock Salt', cursive;font-size:1.2em;margin-bottom:20px;text-align:left;color:#cc3b8b}.project-desc{margin-bottom:20px;text-align:left;height:auto}.project-icon{font-size:25px}.display-none{display:none}.fa-icon{margin:10px;left:-8px;position:relative}a{color:#000}a:hover{color:#cc3b8b}nav ul{list-style-type:none;margin:0;padding:0}nav a{text-decoration:none;color:#000;font-weight:bold;padding:16px 32px}nav a:hover{color:#cc3b8b}nav .desktop-nav{text-align:right;position:relative;top:0;width:100%;background:transparent;z-index:99}.logo{margin-left:20px;margin-top:10px}.desktop-li{display:inline-block;padding-top:16px;padding-bottom:16px;list-style:none}.res-menu{font-size:30px;vertical-align:middle;text-align:center;color:#000}.burger-btn{width:50px;height:50px;background:transparent;border:none;display:none;position:fixed;top:0;right:30px;z-index:100}.side-menu{display:block;background-color:white;box-shadow:2px 0px 5px rgba(0,0,0,0.5);width:300px;height:100vh;top:0;right:0;position:fixed;z-index:99;-webkit-transform:translateX(100%);transform:translateX(100%);transition:-webkit-transform 0.3s ease-out;transition:transform 0.3s ease-out;transition:transform 0.3s ease-out, -webkit-transform 0.3s ease-out}.side-menu.open{-webkit-transform:translateX(0px);transform:translateX(0px)}.back-drop{position:fixed;width:100%;height:100vh;background:rgba(0,0,0,0.8);top:0;right:0;z-index:98;transition:0.3s ease-out}.mobile-ul{margin-top:50px}.mobile-li{display:block;padding:20px}@media only screen and (max-width: 770px){.burger-btn{display:block}.desktop-nav{display:none}}@media only screen and (min-width: 771px){.side-menu{display:none}.back-drop{display:none}}.home-container{max-width:895px}.hacky-block{min-height:465px}.title-txt{margin:auto;width:100%;font-size:6vw;text-align:center;color:#000;text-shadow:2px 2px 4px #3b2a32;font-family:'Rock Salt', cursive}.sub-title{font-size:3vw;text-align:center;color:#cc3b8b}.vertical-align-middle{position:absolute;left:25%;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}@media only screen and (max-width: 700px){.vertical-align-middle{left:10%;top:40%}.title-txt{font-size:9vw}.sub-title{font-size:5vw}}.dkNav{color:#000}.ltNav{color:#000}.square{background-image:linear-gradient(to right, #cc3b8b, #421575, #cc3b8b, #421575);box-shadow:2px 2px 4px #3b2a32;width:200px;font-size:16px;font-weight:600;color:#fff;cursor:pointer;margin:20px;height:55px;text-align:center;border:none}button:active{outline:none}button:focus{outline:none}.center-align{text-align:center}.switch-off{background-size:300% 100%;text-shadow:0 0 10px #fff,0 0 20px #fff,0 0 30px #f696e0,0 0 40px #f77cd6,0 0 50px #e154b4,0 0 60px #db4292,0 0 70px #cc3b8b;moz-transition:all .4s ease-in-out;transition:all .4s ease-in-out;box-shadow:0px 0px 55px 20px #f77cd6;-webkit-animation:btnGlow 3s ease-in-out infinite alternate;animation:btnGlow 3s ease-in-out infinite alternate;border-radius:50px}.switch-on{background-size:300% 100%;border-radius:50px;moz-transition:all .4s ease-in-out;transition:all .4s ease-in-out}.switch-on:hover{background-position:100% 0;moz-transition:all .4s ease-in-out;transition:all .4s ease-in-out}.switch-off:hover{background-position:100% 0;moz-transition:all .4s ease-in-out;transition:all .4s ease-in-out}.bg-light{position:absolute;top:0;left:0;width:100vw;height:100vh;z-index:0;transition:background-color .5s ease}.light-off{background-color:black;background-image:url(/static/media/purpleneon.fb160e18.png);color:#fff;width:100vw;height:100vh;background-repeat:no-repeat;background-position:center;background-size:cover}.light-on{background-color:white;color:black;background-image:url(/static/media/whitewall.03923e52.png);background-repeat:no-repeat;background-position:center;background-size:cover;width:100vw;height:100vh}.textGlow{color:#fff;text-shadow:0 0 10px #fff,0 0 20px #fff,0 0 30px #f696e0,0 0 40px #f77cd6,0 0 50px #e154b4,0 0 60px #db4292,0 0 70px #cc3b8b;-webkit-animation:textGlow 1s ease-in-out infinite;animation:textGlow 1s ease-in-out infinite}@-webkit-keyframes textGlow{from{text-shadow:0 0 10px #fff,0 0 20px #fff,0 0 30px #f696e0,0 0 40px #f77cd6,0 0 50px #e154b4,0 0 60px #db4292,0 0 70px #cc3b8b}to{text-shadow:0 0 20px #fff,0 0 30px #ff4da6,0 0 40px #ff4da6,0 0 50px #ff4da6,0 0 60px #ff4da6,0 0 70px #ff4da6,0 0 80px #cc3b8b}}@-webkit-keyframes btnGlow{0%{background-color:#fff}20%{background-color:#f696e0}40%{background-color:#f77cd6}60%{background-color:#e154b4}80%{background-color:#db4292}100%{background-color:#cc3b8b}}.aboutImg{width:250px;height:250px;border-top-left-radius:50% 50%;border-top-right-radius:50% 50%;border-bottom-right-radius:50% 50%;border-bottom-left-radius:50% 50%;object-fit:cover;display:block;margin-top:75px;margin-bottom:75px;margin-left:auto;margin-right:auto}.icon-container{margin:75px;text-align:center}.about-icon{font-size:40px;padding:10px;color:#000;margin:0 auto}.about-icon:hover{color:#bd2453}.arch-project-img{width:100%;height:200px;object-fit:cover;background-position:10% 50%}.arch-grid-container{display:grid;grid-template-columns:repeat(auto-fit, minmax(220px, 1fr));grid-gap:1rem}footer{height:60px;width:100%;background-color:transparent;color:#767676;text-align:center;position:relative;left:0;bottom:0;right:0;z-index:2}

